<template>
  <v-container>
    <v-card>
      <v-card-text>
        <v-autocomplete
          v-model="currentStudent"
          :items="students.map((r) => ({ name: r.Name, roll: r.roll }))"
          label="Select student"
          @change="getOnesData"
          item-text="name"
          item-value="roll"
        >
          <template v-slot:item="data">
            <template>
              <v-list-item-content>
                <v-list-item-title v-html="data.item.name"></v-list-item-title>
                <v-list-item-subtitle
                  v-html="data.item.roll"
                ></v-list-item-subtitle>
              </v-list-item-content>
            </template>
          </template>
        </v-autocomplete>
      </v-card-text>
      <v-card-actions>
        <v-btn class="mx-auto primary" large tile>
          <v-icon>mdi-magnify</v-icon> Search</v-btn
        >
      </v-card-actions>
    </v-card>
    <template v-if="currentStudent">
      <v-card color="basil mt-5">
        <v-card-title class="text-center justify-center py-6">
          <h1 class="font-weight-bold">Student Details</h1>
        </v-card-title>

        <v-tabs v-model="tab" grow>
          <v-tab> Daily Report </v-tab>
          <v-tab> Check List </v-tab>
          <v-tab> Meetings </v-tab>
        </v-tabs>

        <v-tabs-items v-model="tab" class="pt-3">
          <v-tab-item>
            <div class="mt-5">
              <v-sheet max-width="400" class="mx-auto row transparent">
                <v-text-field
                  type="month"
                  outlined
                  dense
                  label="Select Month"
                  v-model="month"
                  @change="filterByMonth"
                ></v-text-field>
                <v-btn class="primary mx-1" large>apply</v-btn>
              </v-sheet>
            </div>
            <div class="mx-auto text-center">
              <v-btn
                class="primary mx-auto mb-2"
                large
                @click="charts = !charts"
                >Charts</v-btn
              >
            </div>
            <v-card v-if="dailydata.length > 0">
              <v-card-text>
                <v-simple-table>
                  <thead>
                    <tr>
                      <th>Date</th>
                      <th>Hours</th>
                      <th>Pages</th>
                      <th>Efficiency</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="(d, i) in dailydata" :key="i">
                      <td>{{ new Date(d.date).toDateString() }}</td>
                      <td>{{ d.hour }}</td>
                      <td>{{ d.page }}</td>
                      <td>{{ Number(d.efficiency).toFixed(2) }}</td>
                    </tr>
                  </tbody>
                </v-simple-table>
              </v-card-text>
            </v-card>
            <div class="text-center" v-else>
              <p class="mx-auto">No data found.</p>
            </div>
          </v-tab-item>
          <v-tab-item>
            <div class="text-center my-2">
              <v-progress-circular
                color="light-blue"
                :size="100"
                :width="20"
                :value="totalper.toFixed(2)"
                striped
                class="mx-auto"
              >
                <strong>{{ totalper.toFixed(2) }}%</strong>
              </v-progress-circular>
            </div>
            <v-data-table
              :items="checklists"
              :items-per-page="checklists.length"
              :loading="checklists.length < 1"
              hide-default-footer
              :headers="headers"
            >
              <template v-slot:item.progressper="{ item }">
                <v-progress-circular :value="item.progressper" color="green">
                  <small>{{ item.progressper }}%</small>
                </v-progress-circular>
              </template>
            </v-data-table>
          </v-tab-item>
          <v-tab-item>
            <v-list-item v-for="(p, i) in meetings" :key="i">
              <v-list-item-content>
                <div class="black white--text pa-2 my-2">
                  <p>Meeting ID: {{ p.meeting_id }}</p>
                  <p>Reason: {{ p.reason }}</p>
                  <p>Date & Time: {{ p.datetime }}</p>
                  <v-btn :href="p.link" :target="_blank" class="indigo" dark
                    >Meeting Link</v-btn
                  >
                </div>
              </v-list-item-content>
            </v-list-item>
          </v-tab-item>
        </v-tabs-items>
      </v-card>
    </template>
    <v-dialog v-model="charts" max-width="600" v-if="dailydata.length > 0">
      <v-card>
        <v-card-text>
          <study-chart :dailydata="dailydata"></study-chart>
        </v-card-text>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
import StudyChart from "@/components/StudyChart";
export default {
  components: { StudyChart },
  data() {
    return {
      charts: false,
      meetings: [],
      tab: null,
      month: "",
      dailydata: [],
      dayData: {
        hour: 0,
        page: 0,
      },
      submitted: false,
      loading: false,
      alldays: [],
      students: [],
      currentStudent: "",
      checklists: [],
      headers: [
        { text: "Subject", value: "Subject" },
        { text: "Chapter", value: "Chapters" },
        { text: "Progress", value: "progress" },
        { text: "Percentage", value: "progressper" },
      ],
      totalper: 0,
    };
  },
  methods: {
    getQuestions() {
      fetch(
        "https://script.google.com/macros/s/AKfycbyFiaEKIjCgIHNE5G2b8m2lZ4qkCjA20uXftNqJjsk5qtqcsfQ_0lNhU84ZQERT9SjGxg/exec"
      )
        .then((res) => res.json())
        .then((data) => {
          this.meetings = data.routines
            .map((r) => ({
              ...r,
              name: this.students.find((s) => s.roll == r.roll).Name,
            }))
            .filter((s) => s.roll == this.currentStudent)
            .sort((a, b) => b.id - a.id);
        });
    },
    getChecklists() {
      let sheet = this.students.find((s) => s.roll == this.currentStudent);

      fetch(
        `https://script.google.com/macros/s/AKfycbzFgqSH8kZIIIKtH6C7P7TzEMfJgXoiNP2VdCZf7n_hLxyq3OLFMEUtuBSgIY3agt7i/exec?sheet=${sheet.sheet.substr(
          39,
          44
        )}&type=progress`
      )
        .then((res) => res.json())
        .then((data) => {
          let pers = 0;
          this.checklists = data.routines.map((d) => {
            let c = this.getProgress(d);
            pers += c["progressper"];
            return c;
          });
          this.totalper = pers / this.checklists.length;
        });
      this.getQuestions();
    },
    getProgress(data) {
      let count = 0;
      for (const k in data) {
        if (["video", "book", "note", "qbank"].includes(k)) {
          if (data[k]) {
            count++;
          }
        }
      }

      switch (count) {
        case 1:
          data["progress"] = "More to Go";
          break;
        case 2:
          data["progress"] = "Study Hard";
          break;
        case 3:
          data["progress"] = "Keep it up";
          break;
        case 4:
          data["progress"] = "Almost There";
          break;
        case 5:
          data["progress"] = "Chapter Complete";
          break;
        default:
          data["progress"] = "No progress";
      }

      data["progressper"] = count * 20;
      return data;
    },
    filterByMonth() {
      this.dailydata = this.alldays.filter(
        (d) => new Date(d.date).getMonth() == new Date(this.month).getMonth()
      );
    },
    editReport(d) {
      this.dayData = d;
      this.submitted = true;
    },
    getOnesData() {
      this.getChecklists();
      let sheet = this.students.find((s) => s.roll == this.currentStudent);

      fetch(
        `https://script.google.com/macros/s/AKfycbw_kdcD4HYnkByFmpIN-zUYWLeQmoqMiXcAIPc_IVtVs4h0p3NN77U18jFtwKB8TWMknw/exec?sheet=${sheet.sheet.substr(
          39,
          44
        )}&type=dailydata`
      )
        .then((res) => res.json())
        .then((data) => {
          let dp = data.routines
            .filter((r) => r.date)
            .sort((a, b) => b.id - a.id);
          this.dailydata = dp.filter(
            (d) => new Date(d.date).getMonth() == new Date().getMonth()
          );
          this.alldays = dp;
        });
    },

    getData() {
      fetch(
        "https://script.google.com/macros/s/AKfycbxe4ivZ8K8s9VjqQO8-XdENxSFRHZQusf6xzb5IECfH7j-oZj6aFpX5XRjnMt0zCPAP8w/exec"
      )
        .then((res) => res.json())
        .then((data) => {
          this.students = data.students;
        });
    },
  },

  created() {
    this.getData();
  },
};
</script>

<style lang="scss" scoped>
.problem_solving {
  font-size: 16px;

  border-radius: 5px;
  background: linear-gradient(145deg, #c2c5d6, #e7eaff);
  box-shadow: 2px 2px 6px #b8baca, -2px -2px 6px #f8fcff;

  div:first-child {
    background: rgb(221, 224, 217);
    border-radius: 4px;
    padding: 10px;
  }
  div:nth-child(2) {
    background: rgb(133, 243, 142);

    border-radius: 4px;
    padding: 10px;
  }
  div:last-child {
    margin-top: 10px;
    border-radius: 4px;
    padding-top: 15px;
    background: rgb(241, 238, 238);
  }
}
</style>